:root {
  --main-color: #9bb537;
  --primary-text-color: #050505;
  --primary-text-color-08: rgba(5, 5, 5, .8);
  --primary-text-color-05: rgba(5, 5, 5, .5);
  --primary-text-color-03: rgba(5, 5, 5, .3);
  --primary-text-color-02: rgba(5, 5, 5, .2);
  --dark-th-text-color-08: rgba(255, 255, 255, .8);
  --dark-th-text-color-05: rgba(255, 255, 255, .5);
  --dark-th-text-color-03: rgba(255, 255, 255, .3);
  --light-text-color: #f8f8f8;
  --switch-color: #cecdcd;
  --input-label-error: #e74a3b;
  --input-label-correct: #3cbc81;
  --bgc-background: #f8f8f8;
  --ident: 16px;
  --items: 3;
  --transition-set: 2s cubic-bezier(.4, 0, .2, 1);
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  color: inherit;
  height: 0;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button {
  -webkit-appearance: button;
}

[type="button"] {
  -webkit-appearance: button;
}

[type="reset"] {
  -webkit-appearance: button;
}

[type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button {
  height: auto;
}

::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

body {
  background-color: var(--bgc-CH-background);
  transition: background-color var(--transition-set);
  transition: color var(--transition-set);
  font-family: Inter, sans-serif;
}

body.overflowHidden {
  overflow: hidden;
}

main {
  padding-bottom: 80px;
}

.page {
  overflow: hidden;
}

.hidden {
