}

.card-set {
  gap: var(--ident);
  flex-wrap: wrap;
  display: flex;
}

.items-set {
  flex-basis: calc((100% - var(--ident) * (var(--items)  - 1)) / var(--items));
}

.btn {
  cursor: pointer;
  border: 1px solid var(--main-color);
  background: none;
  border-radius: 15px;
  padding: 14px 28px;
  font-size: 14px;
  font-weight: 500;
}

.btn:hover {
  background-color: var(--main-color);
  color: var(--light-text-color);
  border: 1px solid var(--main-color);
  transition: all .3s;
}

.btn-outline {
  color: var(--primary-text-color);
}

.btn-outline-grey {
  color: var(--primary-text-color-05);
  border: 1px solid rgba(5, 5, 5, .2);
  font-size: 18px;
}

.btn-outline-remove {
  color: var(--input-label-error);
  border: 1px solid var(--input-label-error);
  transition: all .3s;
}

.btn-outline-remove:hover {
  background-color: var(--input-label-error);
  border: 1px solid var(--input-label-error);
  color: var(--light-text-color);
}

.btn-outline-grey:hover {
  color: var(--light-text-color);
}

.btn-primary {
  background-color: var(--main-color);
  color: var(--light-text-color);
}

.btn-primary:hover {
  color: var(--primary-text-color);
}

.btn.btn-active {
  background-color: var(--main-color);
  border: 1px solid var(--main-color);
  box-shadow: 1px 1px 15px var(--main-color);
  color: var(--light-text-color);
}

