  stroke: var(--primary-text-CH-color);
}

.burger-menu {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
}

.burger-menu-icon {
  stroke: var(--primary-text-CH-color);
  width: 32px;
  height: 32px;
}

.switcher-input[type="checkbox"] {
  visibility: hidden;
  width: 0;
  height: 0;
  display: none;
}

.input-label {
  cursor: pointer;
  text-indent: -9999px;
  background: #cecdcd;
  border-radius: 18px;
  width: 46px;
  height: 20px;
  display: block;
  position: relative;
}

.input-label:after {
  content: "";
  background: #f8f8f8;
  border-radius: 18px;
  width: 18px;
  height: 18px;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.switcher-input:checked + .input-label {
  background: #9bb537;
}

.switcher-input:checked + .input-label:after {
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

.input-label:active:after {
  width: 26px;
}

.menu-toggle {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  outline: none;
  justify-content: center;
  align-items: center;
  min-width: 40px;
  min-height: 40px;
  margin: 0;
  padding: 0;
  display: flex;
}

@media (min-width: 768px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle:hover, .menu-toggle:focus {
  background-color: rgba(0, 0, 0, .1);
}

.menu-container {
  background-color: var(--bgc-CH-background-mob-menu);
  z-index: 999;
  padding: 200px 0 0;
  transition: transform .25s ease-in-out;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.block {
  overflow: hidden;
}

.menu-container .menu-toggle {
  color: #fff;
  position: absolute;
  top: 16px;
  right: 16px;
}

