@media screen and (min-width: 768px) {
  .popular-mobile {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;

    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;

    display: none;
  }
}

.popular {
  margin-top: 80px;
}
@media screen and (max-width: 768px) {
  .popular.main {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    margin-top: 80px;

    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}

.visually-hidden-555 {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.popular-group-title {
  color: var(--primary-text-CH-color);
  font-weight: 600;
  text-transform: uppercase;

  font-size: 18px;
  line-height: calc(24 / 18);

  margin-bottom: 40px;
}
.popular-list {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.popular-item {
  cursor: pointer;
  list-style: none;
  display: flex;
  gap: 16px;
}

.popular-img {
  border-radius: 8px;
}
.popular-title {
  font-size: 14px;
  font-weight: 600;
  line-height: calc(18 / 14);
  text-transform: uppercase;
  color: var(--primary-text-CH-color);

  overflow-wrap: normal;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.popular-description {
  color: var(--primary-text-CH-color-08);

  font-size: 12px;
  line-height: calc(16 / 12);

  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;

  padding-top: 8px;
}

@media screen and (min-width: 768px) {
  .popular {
    margin-top: 64px;
  }
  .popular-img {
    width: 48px;
    height: 48px;
  }
  .popular-group-title {
    margin-bottom: 32px;
  }
  .popular-list {
    gap: 16px;
  }
  .popular-item {
    gap: 8px;
  }
  .popular-description {
    font-size: 10px;
    line-height: calc(14 / 10);

    -webkit-line-clamp: 3;
  }
}

@media screen and (min-width: 1280px) {
  .popular-img {
    width: 64px;
    height: 64px;
    border-radius: 8px;
  }
  .popular-group-title {
    color: var(--primary-text-CH-color);
    font-size: 24px;
    font-weight: 600;
    line-height: calc(28 / 24);
    text-transform: uppercase;

    margin-bottom: 32px;
  }
  .popular-list {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }
  .popular-item {
    list-style: none;
    display: flex;
    gap: 16px;
  }
  .popular-containet-description {
    height: 100%;
  }
  .popular-title {
    font-size: 14px;
    line-height: calc(18 / 14);
  }
  .popular-description {
    color: var(--primary-text-CH-color-08);

    font-size: 10px;
    line-height: calc(14 / 10);

    display: -webkit-box;

    -webkit-box-orient: vertical;
    overflow: hidden;
  }
}
